.home-page {
	width: 100%;
	height: 100vh;
	position: relative;

	@include max(md) {
		padding-top: vw2(71);
	}

	@include max(sm) {
		height: auto;
		padding-top: vw3(48);
	}

}

.section1 {
	position: absolute;
	left: 0;
	top: 0;
