			width: 100%;
		}
	}

	&__house {
		width: 100%;
		position: absolute;
		left: 0;
		top: vw(178);
		will-change: transform, top;
		transform-style: preserve-3d;

		@include max(md) {
			top: vw2(121);
			translate: none;
			rotate: none;
			scale: none;
			transform: translate(-7.4%, 0%);
			width: vw2(1080);
			height: vw2(1236);
		}

		@include max(sm) {
			transform: none;
			width: 100%;
			height: auto;
			top: vw3(66);
		}

		img {
			width: 100%;

			@include max(md) {
				height: 100%;
				object-position: center top;
				object-fit: cover;
			}
		}

		img {
			width: 100%;
		}
	}

	&__dates {
		display: flex;
		align-items: center;
		font-size: vw(16);
		line-height: 1.92;
		position: absolute;
		top: vw(521);
		left: 50%;
		font-weight: 450;
		transform: translateX(-60%);
		will-change: transform, opacity;
		transform-style: preserve-3d;

		@include max(md) {
			font-size: vw2(33);
			top: vw2(244);
			transform: translateX(-50%);
		}

		@include max(sm) {
			font-size: vw3(12);
			line-height: 2.57;
			transform: none;
			top: vw3(72);
			left: vw3(20);
		}

		&-item {
			&:not(:first-child) {
				margin-left: vw(208);

				@include max(md) {
					margin-left: vw2(22);
				}

				@include max(sm) {
					margin-left: vw3(22);
				}
			}
		}
	}

	&__circle {
		width: vw(336);
		height: vw(336);
		position: absolute;
		left: vw(1311);
		top: vw(344);
		opacity: 1;
		pointer-events: none;
		will-change: transform, opacity;
		transform-style: preserve-3d;

		@include max(md) {
			width: vw2(183);
			height: vw2(183);
			left: vw2(502);
			top: vw2(372);
		}

		@include max(sm) {
			width: vw3(93);
			height: vw3(93);
			left: vw3(194);
			top: vw3(53);
		}

		&-inner {
			width: 100%;
			height: 100%;
			position: relative;
			display: flex;
			align-items: center;
			justify-content: center;
			background: $colorBlue;
			border-radius: 50%;
			will-change: opacity;
			transform-style: preserve-3d;
		}

		&-outside {
			width: vw(318);
			animation: rotateCircle 15s infinite linear;

			@include max(md) {
				width: vw2(174);
			}

			@include max(sm) {
				width: vw3(90);
			}
		}

		&-inside {
			position: absolute;
			left: 50%;
			top: 50%;
			transform: translateX(-50%) translateY(-50%);
			width: vw(172);

			@include max(md) {
				width: vw2(95);
			}

			@include max(sm) {
				width: vw3(47);
			}
		}
	}

	&__scroll {
		font-weight: 450;
		font-size: 16px;
		line-height: 1.92;
		text-transform: uppercase;
		color: #404040;
		display: none;

		span {
			display: block;
			transform: rotate(-90deg);
		}
	}
}


@keyframes rotateCircle {
	0% {
		transform: rotate(0)
	}

	100% {
		transform: rotate(-360deg)
	}
}



