				}

				@include max(sm) {
					padding-left: vw3(16);
				}
			}

			.section3__overlay {
				z-index: 6;
			}
		}

		&--4 {
			padding-left: vw(925);
			will-change: transform, left, top;
			transform-style: preserve-3d;

			@include max(md) {
				padding-left: 0;
			}

			&::after {
				content: "";
				width: 100%;
				height: 100%;
				position: absolute;
				left: 0;
				top: 0;
				z-index: 1;
				display: block;
				background: url(../img/p6-6.webp) right top no-repeat;
				background-size: cover;
				mix-blend-mode: color-burn;

				@include max(md) {
					display: none;
				}
			}

			&::before {
				content: "";
				width: 100%;
				height: 100%;
				position: absolute;
				left: 0;
				top: 0;
				z-index: 1;
				display: block;
				background: linear-gradient(256.45deg, #CECECE 33.71%, rgba(231, 231, 231, 0) 59.71%);

				@include max(md) {
					display: none;
				}
			}

			.section3__text {
				width: auto;
				z-index: 3;
				display: block;
				position: absolute;
				left: 0;
				top: 0;

				@include max(md) {
					position: static;
				}

				.section3__text-inner {
					width: vw(516);

					@include max(md) {
						width: auto;

						span {
							display: none;
						}
					}
				}
			}

			.section3__image {
				position: relative;
				z-index: 4;
				will-change: transform;
				transform-style: preserve-3d;
			}

			.section3__label {
				z-index: 2;
				position: absolute;
				bottom: vw(140);
				left: vw(70);

				@include max(md) {
					display: none;
				}

				.section3__label-item {
					will-change: transform;
					transform-style: preserve-3d;

					&--1 {
						width: vw(786);
						margin-left: vw(800);
					}

					&--2 {
						width: vw(1443);
						margin-top: vw(40);
						margin-left: vw(75);
					}

					&--3 {
						width: vw(1431);
						margin-top: vw(-52);
					}
				}
			}

			.section3__year {
				z-index: 5;

				@include max(md) {
					padding-left: vw2(20);

					@include max(sm) {
						padding-left: vw3(16);
					}
				}
			}

			.section3__mobile {
				display: none;

				@include max(md) {
					display: block;
					padding-left: vw2(20);
					padding-bottom: vw2(50);

					@include max(sm) {
						padding-left: vw3(16);
						padding-bottom: vw3(50);
					}
				}

				&-label {
					@include max(md) {
						width: vw2(520);
					}

					@include max(sm) {
						width: vw3(220);
					}
				}
			}

			.section3__overlay {
				z-index: 6;
			}
		}
	}

	&__year {
		font-size: vw(126);
		line-height: 1.09;
		letter-spacing: -0.06em;
		padding-right: 0.06em;
		color: $colorGray;
		position: absolute;
		right: vw(45);
		top: vw(33);
		font-family: $mainFont;

		@include max(md) {
			font-size: vw2(90);
			position: relative;
			left: 0;
			top: 0;
			padding: vw2(10) 0 vw2(50);
		}

		@include max(sm) {
			font-size: vw3(38);
			padding: vw3(11) 0 vw3(54);
			margin-left: vw3(-4);
		}
	}

	&__overlay {
		position: absolute;
		left: 0;
		top: 0;
		width: 100%;
		height: 100%;
		background: #000;
		opacity: 0;
		z-index: 1;

		@include max(sm) {
			display: none;
		}
	}
}