.y1990 {
	width: 100%;
	height: 100vh;
	position: relative;

	@include max(md) {
		padding-top: vw2(71);
	}

	@include max(sm) {
		height: auto;
		padding-top: vw3(48);
		overflow: hidden;
	}

	.yearConner {
		@include max(md) {
			font-size: vw(38, 320);
			margin-left: vw(16, 320);
			padding-bottom: vw(84, 320);
		}
	}

	.textBlock {
		@include max(md) {
			font-size: vw(14, 320);
			line-height: 121%;
			bottom: auto;
		}
	}

	.blockTitle {
		&--desc {
			@include max(md) {
				display: none;
			}
