}

.y6-110 {
	position: absolute;
	top: 0;
	left: 0;
	width: 100%;
	height: 100%;
	overflow: hidden;
	background: #AAAAAA;
	z-index: 150;
	will-change: transform, opacity, top;
	transform-style: preserve-3d;

	@include max(md) {
		height: 80vw;
		z-index: 110;
	}

	@include max(sm) {

		position: relative;
		height: auto;
		padding-bottom: vw3(120);
		background: #141414;
	}

	&::before {
		content: "";
		z-index: 1;
		position: absolute;
		left: 0;
		top: 0;
		width: 100%;
		height: 100%;
		display: block;
		background: #AAAAAA;
		pointer-events: none;
		@include max(sm) {
			background: #141414;
		}
	}

	&__numbers {
		@include years-row;
		will-change: transform, opacity;
		transform-style: preserve-3d;

		&-number {
			@include years-number;
			background: none;
			-webkit-background-clip: inherit;
			background-clip: inherit;
			-webkit-text-fill-color: unset;
			color: #ffffff;

			@include max(sm) {
				color: #fff
			}
		}

		&-line {
			@include years-line;
			background: #ffff;

			@include max(sm) {
				background: #ffff;
			}
		}
	}

	&__photos {
		position: absolute;
		width: 100%;
		height: 100%;
		z-index: 8;
		top: 0;
		left: 0;
		will-change: opacity;
		transform-style: preserve-3d;

		@include max(sm) {
			display: none;
		}

		&-item {
			left: 0;
			opacity: 0.2;
			position: absolute;
			will-change: transform, left;
			transform-style: preserve-3d;

			&--1 {
				width: vw(525);
				bottom: vw(151);

				@include max(md) {
					width: vw2(525);
				}
			}

			&--2 {
				width: vw(505);
				top: vw(78);

				@include max(md) {
					width: vw2(280);
					top: vw2(78);
				}
			}

			&--3 {
				width: vw(426);
				bottom: vw(118);

				@include max(md) {
					width: vw2(426);
				}
			}
		}
	}

}

